body {
  color: #1f2937;
  background-color: #f3f4f6;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, sans-serif;
}

main {
  height: 100vh;
  display: flex;
  overflow: hidden;
}

aside {
  background: #fff;
  border-right: 1px solid #e5e7eb;
  width: 20%;
  padding: 1rem;
  overflow-y: auto;
}

section {
  background-color: #f9fafb;
  width: 80%;
  padding: 1.5rem;
  overflow-y: auto;
}

h1 {
  margin-bottom: 1rem;
  font-size: 1.75rem;
}

h2 {
  margin-bottom: .75rem;
  font-size: 1.25rem;
}

form {
  background-color: #fff;
  border-radius: 8px;
  margin-bottom: 1rem;
  padding: 1rem;
  box-shadow: 0 1px 3px #0000000d;
}

input, select {
  box-sizing: border-box;
  border: 1px solid #d1d5db;
  border-radius: 4px;
  width: 100%;
  margin-bottom: .5rem;
  padding: .4rem .6rem;
  font-size: .9rem;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: .4rem .8rem;
  font-size: .9rem;
  font-weight: 600;
  transition: background-color .2s;
}

button:hover {
  opacity: .95;
}

.bg-blue-500 {
  color: #fff;
  background-color: #3b82f6;
}

.bg-green-600 {
  color: #fff;
  background-color: #16a34a;
}

.bg-red-600 {
  color: #fff;
  background-color: #dc2626;
}

.text-sm {
  font-size: .85rem;
}

.text-gray-600 {
  color: #4b5563;
}

.text-gray-500 {
  color: #6b7280;
}

.text-red-600 {
  color: #dc2626;
}

.grid {
  gap: 1rem;
  display: grid;
}

.grid-cols-1 {
  grid-template-columns: 1fr;
}

@media (width >= 768px) {
  .md\:grid-cols-3 {
    grid-template-columns: repeat(3, 1fr);
  }
}

.task-column {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  min-height: 500px;
  padding: 1rem;
  display: flex;
  box-shadow: 0 1px 3px #0000000d;
}

.task-card {
  background-color: #fff;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  margin-bottom: 1rem;
  padding: 1rem;
  box-shadow: 0 1px 3px #0000000d;
}

.task-list {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.task-form-wrapper {
  border-top: 1px solid #e5e7eb;
  margin-top: 1rem;
  padding-top: 1rem;
}

.inline-form {
  border-top: 1px solid #e5e7eb;
  margin-top: auto;
  padding-top: .5rem;
}

.filter-panel {
  background: #fff;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  max-width: 640px;
  margin: 0 auto 1.5rem;
  padding: .75rem;
  display: flex;
  box-shadow: 0 1px 3px #0000000d;
}

.filter-panel select, .filter-panel input {
  border: 1px solid #d1d5db;
  border-radius: 4px;
  min-width: 180px;
  padding: .35rem .5rem;
  font-size: .85rem;
}

.icon-button {
  cursor: pointer;
  border: none;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  padding: .4rem .6rem;
  font-size: 1rem;
  transition: opacity .2s;
  display: inline-flex;
}

.icon-button:hover {
  opacity: .9;
}

.icon-button i {
  font-size: 1.2rem;
}

.task-card .icon-button {
  margin-right: .4rem;
}

.task-card .icon-button:last-child {
  margin-right: 0;
}

.category-box {
  background-color: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  margin-top: 1.5rem;
  padding: 1rem;
  box-shadow: 0 1px 3px #0000000d;
}
